import React from "react"

export default  class RightCarousel extends React.Component{
    constructor(){
        super()
        this.state ={
            current:0,
            list:['要闻','财经','体育','娱乐','房产','科技']
        }

    }
    currentClass(val,index){
        return  index==this.state.current?"activeNavItem":""
    }
    contentClass(val,index){
        return index ==this.state.current?"nav_ContentActive":"nav_Content"
    }
    changeNavItemHandle(e){
        //if(e.target.className!='activeNavItem'){return}
        // e.stopPropagation()
        var targetData =  e.target.getAttribute("data")
        this.setState({
            current:targetData
        })
    }
    render(){
        return(

            <div style={{clear:'both'}}>
                <ul className="RightCarousel">
                    {this.state.list.map((val,index)=>{
                        console.log(val)

                        return(
                            <li key={index} className={this.currentClass(val,index)}
                                data={index}
                                onMouseOver={this.changeNavItemHandle.bind(this)}
                            >
                                {val}
                                <div className={this.contentClass(val,index)}>{index}</div>
                            </li>
                        )
                    })}
                </ul>

            </div>


        )
    }
}


